    import dataListItem from "@/components/pageDataList";
    import screen from '@/components/screen/screen'
    let globalData = getApp().globalData;
    let utils = require('@/util/utils.js');
    let request = utils.request;
    export default {
        props: ['update'],
        components: {
            dataListItem,
            screen
        },
        data() {
            return {
                current: 0,
                dataList0: [],
                dataList1: [],
                dataIndex0: 1,
                dataIndex1: 1,
                pageNum1: 0,
                pageNum0: 0
            }
        },
        computed: {
            filterQuestion(){
                return this.$store.state.market.filterQuestion;
            }
        },
        // 自动更新
        watch: {
            update() {
                this.current = 0;
                this.pageConfig();
                this.getData();
            }
        },
        methods: {
            // 把页数设为1
            pageConfig: function() {
                this.dataIndex0 = 1;
                this.dataIndex1 = 1;
                this.dataList0 = [];
                this.dataList1 = [];
            },
            swiperChange: function(e) {
                this.current = e.detail.current;
                this.pageConfig();
                this.getData();
            },
            // 父组件传值
            getCurrent(current) {
                this.current = current;
                this.pageConfig();
                this.getData();
            },
            // 修改筛选信息
            getFilter() {
                this.pageConfig();
                this.getData();
            },
            // 刷新
            getState() {
                this.pageConfig();
                this.getData();
            },
            // 加载更多
            againMore() {
                let index = 1;
                if (this.current) {
                    if (this.pageNum1 == this.dataIndex1) {
                        return;
                    } else {
                        this.dataIndex1 = this.dataIndex1 + 1;
                    }
                    // this.pageNum1 == this.dataIndex1?'':this.dataIndex1 = this.dataIndex1 + 1;
                    index = this.dataIndex1;
                } else {
                    if (this.pageNum0 == this.dataIndex0) {
                        return;
                    } else {
                        this.dataIndex0 = this.dataIndex0 + 1;
                    }
                    // this.pageNum0 == this.dataIndex0?'':this.dataIndex0 = this.dataIndex0 + 1;
                    index = this.dataIndex0;
                }
                this.getData(true);
            },
            // 请求数据       
            getData: function(more) {
                let index = 1;
                if (this.current) {
                    index = this.dataIndex1;
                } else {
                    index = this.dataIndex0;
                }
                this.getDataList(this.current, index, more);
            },
            // 获取数据
            getDataList: function(status, pageindex, more) {
                uni.showNavigationBarLoading();
                let _this = this;
                // 默认请求未解决的
                let option = this.filterQuestion;
                let senddpetid = [];
                for (let item of option.deptSelect) {
                    senddpetid.push(item.deptid);
                }
				console.log(globalData.userinfo)
                let data = {
                    usernumber: globalData.userinfo.usernumber,
                    sdate: option.sdate,
                    edate: option.edate,
                    status,
                    senddpetid,
                    inspectioncode: "",
                    pagesize: option.pagesize,
                    pageindex
                };
                console.log("问题列表数据请求参数:", data)
                request.getQuestionList(data).then(data => {
                    let [err, res] = data;
                    console.log("问题页列表数据：", err, res)
                    if (err == null && res.data.success) {
                        let list = res.data.data;
                        if (status) {
                            more ? _this.dataList1 = _this.dataList1.concat(list) : _this.dataList1 = list;
                            // _this.dataList1 = list;
                            _this.pageNum1 = res.data.pagenum;
                        } else {
                            more ? _this.dataList0 = _this.dataList0.concat(list) : _this.dataList0 = list;
                            // _this.dataList0 = list;
                            _this.pageNum0 = res.data.pagenum;
                        }
                    } else {
                        uni.showToast({
                            icon: "none",
                            duration: 2500,
                            title: "查找失败"
                        })
                        uni.hideNavigationBarLoading();
                        return;
                    }
                    _this.$forceUpdate();
                    uni.hideNavigationBarLoading();
                })
            }
        },
        onShow: function() {
            console.log("question onShow");
        }
    }
